:root {
  font-family: 'helvetica neue', helvetica, sans-serif; }

:root {
  font-family: 'Sen', "helvetica", times, serif; }

body .editor-styles-wrapper {
  font-family: "Sen", "helvetica", times, serif; }

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
.editor-styles-wrapper .h1,
.editor-styles-wrapper .h2,
.editor-styles-wrapper .h3,
.editor-styles-wrapper .h4,
.editor-styles-wrapper .h5,
.editor-styles-wrapper .h6,
.editor-styles-wrapper h1,
.editor-styles-wrapper h2,
.editor-styles-wrapper h3,
.editor-styles-wrapper h4,
.editor-styles-wrapper h5,
.editor-styles-wrapper h6 {
  font-family: "Sen", "helvetica", times, serif;
  font-weight: 700;
  line-height: 1.1; }

.editor-styles-wrapper .c9-heading .h1,
.editor-styles-wrapper .c9-heading .h2,
.editor-styles-wrapper .c9-heading .h3,
.editor-styles-wrapper .c9-heading .h4,
.editor-styles-wrapper .c9-heading .h5,
.editor-styles-wrapper .c9-heading .h6,
.editor-styles-wrapper .c9-heading h1,
.editor-styles-wrapper .c9-heading h2,
.editor-styles-wrapper .c9-heading h3,
.editor-styles-wrapper .c9-heading h4,
.editor-styles-wrapper .c9-heading h5,
.editor-styles-wrapper .c9-heading h6 {
  line-height: 1.1; }

.editor-styles-wrapper .c9-cta .c9-sh p,
.editor-styles-wrapper .c9-heading .c9-h .text-muted {
  font-family: "Sen", "helvetica", times, serif !important; }

.editor-styles-wrapper .mce-content-body.button,
.editor-styles-wrapper .wp-block-button__link,
.editor-styles-wrapper .button,
.editor-styles-wrapper .button-primary,
.editor-styles-wrapper .mce-content-body.button,
.c9-block-cta .c9-cta-button a {
  text-transform: uppercase;
  font-weight: 700;
  font-family: "Sen", "helvetica", times, serif; }

.editor-styles-wrapper .button:hover,
.editor-styles-wrapper .button-primary:hover,
.editor-styles-wrapper .mce-content-body.button:hover,
.editor-styles-wrapper .wp-block-button__link:hover {
  opacity: 0.8;
  color: #ffffff; }

.editor-styles-wrapper p,
.editor-styles-wrapper .container p,
.editor-styles-wrapper .container-narrow p,
.editor-styles-wrapper .container-fluid p,
.editor-styles-wrapper .editor-rich-text .mce-content-body p,
.editor-styles-wrapper .wp-block-paragraph,
.editor-styles-wrapper .wp-block-quote p,
.editor-styles-wrapper .wp-block-file,
.editor-styles-wrapper ul li,
.editor-styles-wrapper ol li,
.editor-styles-wrapper .wp-block-pullquote,
.editor-styles-wrapper .wp-block-pullquote blockquote p {
  font-family: "Sen", "helvetica", times, serif; }

.editor-styles-wrapper .c9-cta .wp-block-button__link {
  font-size: 12px; }

/* consider migrating this to customer-editor style in theme base */
div[data-type="c9-blocks/column-container"] .editor-block-list__block-edit.block-editor-block-list__block-edit .c9-column-innner .wp-block[data-type="core/paragraph"] > .editor-block-list__block-edit.block-editor-block-list__block-edit {
  margin-bottom: 15px;
  margin-top: 15px; }

/* list item additional block styles */
.editor-styles-wrapper .is-style-light-list li,
.editor-styles-wrapper .is-style-light-list li a {
  color: #ffffff; }

/* woocommerce */
.wp-block[data-type="^woocommerce/"] .wc-block-grid__products .wc-block-grid__product,
.edit-post-visual-editor .editor-block-list__block .wc-block-grid__product-title,
.editor-styles-wrapper .wc-block-grid__product-title,
.wc-block-grid__product-title {
  font-size: 16px; }

.editor-styles-wrapper .wp-block[data-type^="woocommerce/"] .wc-block-grid__products .wc-block-grid__product a:not(.wp-block-button__link) {
  text-decoration: none; }

/* image block captions */
@media only screen and (min-width: 1px) {
  .editor-styles-wrapper .wp-block-image figcaption,
  .editor-styles-wrapper .wp-block-image .aligncenter > figcaption,
  .editor-styles-wrapper .wp-block-image .alignleft > figcaption,
  .editor-styles-wrapper .wp-block-image .alignright > figcaption,
  .editor-styles-wrapper .wp-block-image.is-resized > figcaption {
    font-size: 14px;
    font-weight: 700; } }

/* universal line height for paragraphs */
.editor-styles-wrapper p,
.wp-block[data-type="core/paragraph"] p,
.c9 dl,
.entry-content p,
.entry-footer {
  line-height: 1.3; }

